.footComponent{
font-family: cursive;
    background-color:whitesmoke;
    h2{
        color: blue;
        text-align: center;
        font-weight: bolder;

}
.footElement{

    margin:15px 0px;
    box-shadow: 15px 15px 20px;
    padding: 15px;
    h3{
        text-indent: 20px;
        text-shadow: 5px 5px 15px ;
        color: blueviolet;

        button{
          width:80px;
          padding: 5px;
          margin: 5px;
          border: none;
          background-color: rgb(144, 101, 184);
          color: white;
          text-shadow: 5px 5px 15px ;
          font-family: cursive;
        }
    }

    .footSection{

        img{
           width: 100%;
           height: 350px;
           object-fit: cover;
        }

        p{
            text-align: justify;
            padding: 5px;
            line-height: 50px;
        }
    
        small{
            margin: 0px 10px;
        }

       
    }
   


}

}